<template>
  <div id="app">
    <nav class='flex-h space-between' id='nav-bar'>
      <div class='flex-v'>
        <a id='name-logo' v-on:click='seupUI' href="#/">Inca Hernandez</a>
        <p id='slogan'>Arquitectura</p>
      </div>
      <div class="flex-v">
        <ul class="flex-h no-list">
          <li 
            class='navbar-item' 
            v-for="item in navBarItems"
            v-bind:class="{ 'active': isCurrentRoute(item)}" >
            <a v-on:click='seupUI' :href="item.path">{{ item.name }}</a>
          </li>
        </ul>
      </div>
    </nav>
    <router-view id='router-view'></router-view>
    <footer id='footer'>
      <p>Inca Hernandez, all rights reserved 2018</p>
    </footer>
  </div>
</template>
<script>

export default {
  name: 'app',
  data: () => {
    return {
      isCurrentRoute: (item) => {
        return window.location.href.indexOf(item.path) >= 0
      },
      navBarItems: [
        { name: 'PROYECTOS', path: '#/projects' },
        { name: 'DESARROLLOS', path: '#/developments' },
        { name: 'ARTE', path: '#/art' },
        { name: 'PUBLICACIONES', path: '#/publications' },
        { name: 'CONTACTO', path: '#/contact' },
        { name: 'INCA HERNANDEZ', path: '#/personal' }
      ]
    }
  },
  components: {},
  methods: {
    seupUI: () => {
      setTimeout(() => {
        let navBar = document.getElementById('nav-bar')
        let footer = document.getElementById('footer')
        let windowHeight = window.innerHeight
        let margin = 80
        let routerView = document.getElementById('router-view')
        routerView.style.height = windowHeight - (navBar.clientHeight + footer.clientHeight + margin) + "px"
      }, 10)

    },
  },  
  
  mounted: function mounted () {
      this.seupUI()
  }
}
</script>

<style lang="sass">

@import url('https://fonts.googleapis.com/css?family=Oxygen:300,400,700')
@import './public/css/styles'

#name-logo
  height: 32px
  @extend .f-24, .bold

#slogan
  height: 20px
  @extend .f-16, .regular

.navbar-item
  @extend .bold

.navbar-item.active
  color: rgba(0,0,0,.2)

#footer
  margin: 10px 0
  p
    @extend .t-center, .thin, .f-12

nav
  margin-top: 20px
  @extend .container
  li
    padding: 0 12px
    border-right: 1px solid rgba(0,0,0,.5)
    @extend .f-12

#router-view
  border: 1px solid rgba(0,0,0,.01)
  margin: 20px auto
  animation-name: example
  animation-duration: 1s
  overflow: auto
  @extend .container

@keyframes example 
    from 
      opacity: 0
    to 
      opacity: 1


</style>
