import Vue from 'vue'
import App from './App.vue'
import Router from 'vue-router'

//Components
import ProjectsView from './components/ProjectsView.vue'
import ContactView from './components/ContactView.vue'
import PersonalView from './components/PersonalView.vue'
import HomeView from './components/HomeView.vue'

import NotFound from './components/NotFound.vue'


Vue.use(Router);

const routes = [
	{ path: '/', component: HomeView },
	{ path: '/projects', component: ProjectsView, props: {title: 'projects'} },
	{ path: '/developments', component: ProjectsView, props: {title: 'developments'} },
	{ path: '/art', component: ProjectsView, props: {title: 'art'} },
	{ path: '/publications', component: ProjectsView, props: {title: 'publications'} },
	{ path: '/contact', component: ContactView },
	{ path: '/personal', component: PersonalView },
	
	{ path: '/404', component: NotFound },
]

const router = new Router({
	routes: routes
})

Vue.config.productionTip = false

new Vue({
	router: router,
	render: h => h(App)
}).$mount('#app')
